import { Component } from '@angular/core';
import { NavController } from '@ionic/angular';
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  
  constructor(
    private nav: NavController
  ) {
    this.checkSideMenu()
    this.setHospitalName()
  }

  public appPages = [
    { title: 'Dashboard ATK', url: '/atk-dashboard', icon: 'medkit' },
    // { title: 'Outbox', url: '/folder/Outbox', icon: 'paper-plane' },
    // { title: 'Favorites', url: '/folder/Favorites', icon: 'heart' },
    // { title: 'Archived', url: '/folder/Archived', icon: 'archive' },
    // { title: 'Trash', url: '/folder/Trash', icon: 'trash' },
    // { title: 'Spam', url: '/folder/Spam', icon: 'warning' },
  ];

  sideMenu = true
  hospitalName = ""

  setHospitalName() {
    if (localStorage.getItem("user_data")) {
      let userData = JSON.parse(localStorage.getItem("user_data"))
      this.hospitalName = userData["hospname"]
    }
  }

  checkSideMenu() {
    let url = location.href
    let sideMenu = ""
    if (url.includes("sidemenu")) {
      sideMenu = url.split("sidemenu=")[1]
      if (sideMenu.includes("&")) {
        sideMenu = sideMenu.split("&")[0]
      }
    }
    if (sideMenu == 'true') {
      this.sideMenu = true
      localStorage.setItem("side-menu", "true")
    } else if (sideMenu == 'false') {
      this.sideMenu = false
      localStorage.setItem("side-menu", "false")
    }
  }

  logoutButton() {
    localStorage.clear()
    setTimeout(() => {
      this.nav.navigateBack("/map-atk")
    }, 500);
  }
}
