import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'atk-dashboard',
    pathMatch: 'full'
  },
  {
    path: 'folder/:id',
    loadChildren: () => import('./folder/folder.module').then( m => m.FolderPageModule)
  },
  {
    path: 'atk-dashboard',
    loadChildren: () => import('./atk-dashboard/atk-dashboard.module').then( m => m.AtkDashboardPageModule)
  },
  {
    path: 'atk-dashboard-modal',
    loadChildren: () => import('./atk-dashboard-modal/atk-dashboard-modal.module').then( m => m.AtkDashboardModalPageModule)
  },
  {
    path: 'login',
    loadChildren: () => import('./login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'map-atk',
    loadChildren: () => import('./map-atk/map-atk.module').then( m => m.MapAtkPageModule)
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
